export let cloudOption = {
    tooltip: {},
    series: [{
        type: "wordCloud",
        gridSize: 8,
        sizeRange: [10, 150],
        rotationRange: [0, 0],
        shape: "pentagon",
        rotationStep: 45,
        windth: '100%',
        height: '100%',
        drawOutOfBound: true,
        textStyle: {
            color: function () {
                return (
                    "rgb(" + [
                        Math.round(Math.random() * 160),
                        Math.round(Math.random() * 160),
                        Math.round(Math.random() * 160),
                    ].join(",") +
                    ")"
                );
            },
        },
        emphasis: {
            textStyle: {
                shadowBlur: 10,
                shadowColor: "#333",
            },
        },
        data: allWords.map((item, index) => {
            if (index == 0) {
                return {
                    name: item.word,
                    value: item.num,
                    textStyle: {
                        color: "blue",
                    },
                    emphasis: {
                        textStyle: {
                            color: "red",
                        },
                    },
                }
            }
            return { name: item.word, value: item.num }
        }),
    }],
};
