<script>
    import {
        cloudOption,
        cloudOptionClear,
        optiondd,
        optionfp
    } from "@/utils/ppt/index.js"
    export default {
        components: {},
        data() {
            return {
                isV: true,
                currentPage: 1,
                config: {
                    interval: 1000,
                    lastArrow: false,
                    nextArrow: true,
                },
                topArr: [],
                imgs: [require('@/assets/imgs/1.gif')],
                toolGif: [require('@/assets/imgs/tool.gif')],
                dldfMap4: [require('@/assets/imgs/dldf.mp4')],
                appImg: [require('@/assets/imgs/小程序.png')],
                tsMp4: [require('@/assets/imgs/ts.mp4')],
                oneStep: [require('@/assets/imgs/一键.gif')],
                chart: {},
                clearFlag: false,
            };
        },
        mounted() {
            this.chart = echarts.init(document.getElementById("cloud"));
            var chartdd = echarts.init(document.getElementById("echart-dd"));
            var chartfp = echarts.init(document.getElementById("echart-fp"));
            this.chart.setOption(cloudOption);
            chartdd.setOption(optiondd);
            chartfp.setOption(optionfp);
            this.getTopArr(cloudOption); //更新
        },
        methods: {
            getTopArr(option) {
                this.topArr = option.series[0].data.slice(0, 4);
            },
            clearCloud() {
                if (!this.clearFlag) {
                    this.chart.setOption(cloudOptionClear);
                    this.getTopArr(cloudOptionClear); //更新
                } else {
                    this.chart.setOption(cloudOption);
                    this.getTopArr(cloudOption); //更新
                }
                this.clearFlag = !this.clearFlag;
            }
        }
    };
</script>

